* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

ul {
  list-style-type: none;
  margin: 0 0 40px;
  padding: 0;
}

li {
  display: flex;
  align-items: center;  
}

p {
  margin: 0px;
}

img {
  margin-right: 10px;
}
.country-info__label {
  font-size: 16px;
  font-weight: 700;
  margin-right: 10px;
}

.country-info__info {
  font-size: 16px;
}
